.typeOf {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    z-index: 1;
  }
  .typeOf > img {
    max-height: 150px;
    width: 100%;
    object-fit: contain;
    margin: 20px 0 0 15px;
  }
  